        <span class="hljs-attr">stop</span>: {},
        <span class="hljs-attr">positions</span>: []
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([<span class="hljs-keyword">this</span>.$refs.path.createPromise, <span class="hljs-keyword">this</span>.$refs.model.createPromise]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        instances[<span class="hljs-number">0</span>].viewer.zoomTo(instances[<span class="hljs-number">0</span>].viewer.entities)
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        <span class="hljs-keyword">this</span>.terrainProvider = Cesium.createWorldTerrain()
        <span class="hljs-keyword">this</span>.position1 = Cesium.Cartesian3.fromDegrees(<span class="hljs-number">114.0</span>, <span class="hljs-number">40.0</span>, <span class="hljs-number">1.0</span>)
        <span class="hljs-comment">//Enable lighting based on sun/moon positions</span>
        viewer.scene.globe.enableLighting = <span class="hljs-literal">true</span>
        <span class="hljs-comment">//Enable depth testing so things behind the terrain disappear.</span>
