        <span class="hljs-keyword">this</span>.position1 = <span class="hljs-keyword">this</span>.computeCirclularFlight(<span class="hljs-number">-112.110693</span>, <span class="hljs-number">36.0994841</span>, <span class="hljs-number">0.03</span>)
        <span class="hljs-keyword">this</span>.availability = <span class="hljs-keyword">new</span> Cesium.TimeIntervalCollection([
